@charset "UTF-8";
/* ------------------------------------ *\
  ACCORDIONS
\* ------------------------------------ */
.fr-accordion .fr-accordion__btn {
  display: inline-flex;
  flex-direction: row;
  align-items: center;
}

.fr-accordion .fr-accordion__btn {
  overflow: hidden;
  z-index: 1;
}

.fr-accordion {
  position: relative;
}

.fr-accordion__title {
  margin: 0;
  display: block;
  font-size: unset;
  line-height: unset;
}

.fr-accordion .fr-accordion__btn {
  font-size: 1rem;
  line-height: 1.5rem;
  min-height: 3rem;
  padding: 0.75rem 1rem;
  max-width: none;
  max-height: none;
  flex-direction: row-reverse;
  width: 100%;
  text-align: left;
  margin: 0;
  padding: 0.75rem 0;
}

.fr-accordion .fr-accordion__btn::before {
  font-family: icons !important;
  font-style: normal;
  font-weight: normal !important;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
}

.fr-accordion .fr-accordion__btn::before {
  content:  "";
}

.fr-accordion .fr-accordion__btn::before {
  margin-right: -0.25rem;
  margin-left: 0.25rem;
  font-size: 1rem;
}

.fr-accordion .fr-accordion__btn::before {
  margin-right: 0;
  margin-left: auto;
}

.fr-accordion .fr-accordion__btn[aria-expanded="true" i] {
  font-weight: 700;
}

.fr-accordion .fr-accordion__btn[aria-expanded="true" i]::before {
  content:  "";
}

@media (min-width: 48em) {
  .fr-accordion .fr-accordion__btn {
    padding: 0.75rem 1rem;
  }
}

.fr-accordion .fr-collapse {
  --is-list: ;
  transition: visibility 0.3s, padding 0.3s;
}

@media (min-width: 48em) {
  .fr-accordion .fr-collapse {
    margin: 0 -0.25rem;
  }
}

@media (min-width: 48em) {
  .fr-accordion .fr-collapse {
    padding-left: 1rem;
    padding-right: 1rem;
  }
}

.fr-accordion .fr-collapse--expanded {
  padding-top: 1rem;
  padding-bottom: 1.5rem;
}

.fr-accordions-group {
  --is-list: false;
}

.fr-accordions-group > * > .fr-accordion,
.fr-accordions-group > .fr-accordion {
  margin-top: 1px;
}

.fr-accordion {
  box-shadow: 0 1px 0 0 var(--g300), 0 -1px 0 0 var(--g300);
}

.fr-accordion .fr-accordion__btn {
  color: var(--g700);
  background-color: var(--t-plain);
}

@media (hover: hover) and (pointer: fine) {
  .fr-accordion .fr-accordion__btn:not(a):not(:disabled):hover, .fr-accordion a.fr-accordion__btn[href]:hover {
    background-image: linear-gradient(0deg, var(--color-hover), var(--color-hover));
  }
  .fr-accordion .fr-accordion__btn:not(a):not(:disabled):active, .fr-accordion a.fr-accordion__btn[href]:active {
    background-image: linear-gradient(0deg, var(--color-active), var(--color-active));
  }
}

:root:not([data-fr-theme="dark" i]) .fr-accordion .fr-accordion__btn {
  --color-hover: var(--block-color-hover);
  --color-active: var(--block-color-active);
}

:root:not([data-fr-theme="dark" i]) [class*="fr-scheme-light"] .fr-accordion .fr-accordion__btn,
:root:not([data-fr-theme="dark" i]) [class*="--scheme-light-"] .fr-accordion .fr-accordion__btn {
  --color-hover: var(--block-color-hover);
  --color-active: var(--block-color-active);
}

:root:not([data-fr-theme="dark" i]) [class*="fr-scheme-dark"] .fr-accordion .fr-accordion__btn,
:root:not([data-fr-theme="dark" i]) [class*="--scheme-dark-"] .fr-accordion .fr-accordion__btn {
  --color-hover: var(--block-color-hover);
  --color-active: var(--block-color-active);
}

:root[data-fr-theme="dark" i] .fr-accordion .fr-accordion__btn {
  --color-hover: var(--block-color-hover);
  --color-active: var(--block-color-active);
}
